:root {
    --speed: 0.5s;
    --divs: 5;
}

/*Dropdown Menu*/

.filter-container > .open {
    display: block;
    opacity: 1;
    transition: max-height var(--speed) ease-in, opacity var(--speed) ease-in;
    max-height: 500px;
}

.filter-container > .closed {
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: max-height var(--speed) ease-out, opacity var(--speed) ease-out;
}

.title-container > .open {
    transform: rotate(270deg);
    transition: transform var(--speed) ease;
}

.title-container > .closed {
    transform: rotate(90deg);
    transition: transform var(--speed) ease;
}

/*Event Handler Click Me*/

.wideBox {
    position: absolute;
}

.wideBox {
    width: 100%;
    transition: width 1s ease;
}

.narrowBox {
    width: 50%;
    transition: width 1s ease;
}

/*DivSelector*/

.active {
    width: 300%;
    transition: width 1s ease;
}

.inactive {
    width: calc(100% / var(--divs));
    transition: width 1s ease;
}

.active:hover,
.inactive:hover,
.reset:hover {
    filter: brightness(120%);
    /*background-color: #7162AA; !*lighter*!*/
    transition: 0.5s ease;
}

.active:active,
.inactive:active,
.reset:active {
    filter: brightness(90%);
    /*background-color: #442D9B; !*darker*!*/
    transition: 0.75s ease;
}

/*TimeSelector*/

.preflip {
    transition: transform 50ms linear;
}

.flip {
    transform: rotateX(360deg);
}

/*EventTarget*/

.color-transition {
    transition: background-color 1s ease;
}